
.name {
  list-style: none;
  font-size: 12px;
  position: absolute;
  left: 90px;
  color: #c0c0c0;
  overflow: hidden;
}
.name li {
  margin-top: 3px;
}
.name .first {
  color: #333;
  font-size: 16px;
}
.male {
  color: #4cb7ed;
}
.female {
  color: #f17f7f;
}
.fa-icon {
  vertical-align: top;
}



